import React, { Component } from 'react';
import { Breadcrumb, Icon, Select, Form, Button, Input, Upload, Modal, Radio, Checkbox, Row, Col } from 'antd';
import { Link } from 'react-router-dom';

import PageHeader from '../../../component/page_header/page_header';            //
import Menus from '../../../component/menus/menus';                             //
import AddGoodComponent from './addGoodComponent';

import './addGoods.css';                                                        //

const Option = Select.Option;                                                   //
const RadioGroup = Radio.Group;                                                 //

class AddGoodss extends Component {
  constructor() {
    super();
    this.state = {
      previewVisible: false,                                                    //
      previewImage: '',
      fileList: [],                                                             //
      port: false,                                                              //false PCtrue 
      value1: 1,                                                                //
      value2: 1,
    }
  }

  // xuanze shops
  handleChange = (value) => {
    console.log(`selected ${value}`);
  }

  // 
  handleCancel = () => this.setState({ previewVisible: false })
  // 
  handlePreview = (file) => {
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  }
  // 
  handleUpload = ({ fileList }) => {
    console.log(fileList);
    this.setState({
      fileList,
      port: true,
    })
  }

  // 
  radioBtn1 = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      value1: e.target.value,
    });
  }
  radioBtn2 = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      value2: e.target.value,
    });
  }
  // 
  checkboxBtn = (e) => {
    console.log(e);
  }

  // from
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { previewVisible, previewImage, fileList, port } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        {
          port ?
            <div className="ant-upload-text">Mobile</div>
          :
          <div className="ant-upload-text">PC</div>
        }
      </div>
    );
    return(
      <div>
        {/*  */}
        <PageHeader />
        <div className="rawp">
          <div className="contents">
            <div className="contents_left">
              {/*  */}
              <Menus history ={this.props.history} />
            </div>
            <div className="contents_right">
              {/*  */}
              <div className="boxPadding">
                {/*  */}
                <Breadcrumb className="mbx">
                  <Breadcrumb.Item>
                    <Link to="/center_new">
                      <Icon type="home" />
                    </Link>
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    <Link to="/center_new">
                      <Icon type="user" />
                      <span>Center</span>
                    </Link>
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    <Link to="/myGoods">
                      <Icon type="setting" />
                      <span>My Goods</span>
                    </Link>
                  </Breadcrumb.Item>
                  <Breadcrumb.Item>
                    Add Goods
                  </Breadcrumb.Item>
                </Breadcrumb>
              </div>
              {/*  goods */}
              <AddGoodComponent />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const AddGoods = Form.create()(AddGoodss);
export default AddGoods
